/**
 * Emoji 工具函数
 */

// 验证 emoji 编码
export function validateEmoji(code) {
  // 支持 emoji 字符
  if (/^[\p{Emoji}]$/u.test(code)) {
    return true;
  }
  // 支持 Unicode 码点格式 (U+1F60A)
  if (/^U\+[0-9A-F]{4,6}$/i.test(code)) {
    return true;
  }
  return false;
}

// 将 Unicode 码点转换为 emoji 字符
function unicodeToEmoji(code) {
  if (code.startsWith("U+")) {
    const hex = code.slice(2);
    return String.fromCodePoint(parseInt(hex, 16));
  }
  return code;
}

// 创建 emoji
export function createEmoji(code) {
  if (!validateEmoji(code)) {
    return null;
  }
  return unicodeToEmoji(code);
}

// 简单的 Emoji 组件
export function Emoji({ code, size = 24 }) {
  if (!validateEmoji(code)) {
    return null;
  }

  const emoji = unicodeToEmoji(code);

  return {
    type: "span",
    props: {
      style: {
        fontSize: `${size}px`,
        lineHeight: 1,
        display: "inline-block",
      },
      children: emoji,
    },
  };
}
